export default {
	data() {
		return {
			account: '18566333333',
			avarUrl: '',
			imgUrls: [],
			nickName: '',
			password: '',
			password1: '',
			status: 3, //1审核中  2审核失败
			content1: '管理员正在审核您的信息,审核通过后可在个人中心创作者账号上传视频哦',
			content2: '很抱歉,您的身份证信息不清晰，请重新申请'
		};
	},
	methods: {
		//  选择图片
		uploadImg() {
			const vm = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success(res) {
					const tempFilePaths = res.tempFilePaths;
					let flag = true; //所有图片格式均正确
					for (let i = 0; i < tempFilePaths.length; i++) {
						uni.getImageInfo({
							src: tempFilePaths[i],
							success: function(res) {
								if (res.height < res.width) {
									flag = false;
									wx.showModal({
										content: '图片格式不正确（图片高度不得小于宽度）,请裁剪后再进行上传哦',
										showCancel: false,
										success: function(res) {
											if (res.confirm) {
												vm.uploadImg();
												return false;
											}
										}
									});
								}
								// 当所有图片格式均正确
								if (++i == tempFilePaths.length && flag) {
									console.log(tempFilePaths)
									vm.data.imgUrls = vm.$utils.uploadingImg(tempFilePaths);
								}
							}
						})
					}
				}
			})
		},
		goPage(url) {
			this.$utils.goPage(url)
		},
		back() {
			uni.navigateBack()
		}
	}
}
